import React, { Fragment, useState } from 'react';
import { Paragraph, Button } from '@olivahouse/ui';
import { validate } from 'isemail';
import fetchJsonp from 'fetch-jsonp';

import { useStrings, useLanguageCode } from '../I18n';
import { usePricing } from '../Pricing';

import styles from './styles.module.css';

export const Component = () => {
  const [emailValue, setEmailValue] = useState('');
  const [emailError, setEmailError] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);

  const languageCode = useLanguageCode();
  const { currencyCode, currencySymbol, pack, price } = usePricing();

  const strings = useStrings();

  const handleChange = ({ target: { value } }) => {
    setEmailError('');
    setEmailValue(value);
  };

  const handleSubmit = async () => {
    if (!emailValue) {
      setEmailError(strings.WE_NEED_YOUR_EMAIL);
      return;
    }

    const isValid = validate(emailValue);

    if (!isValid) {
      setEmailError(strings.YOUR_EMAIL_DOESNT_LOOK_RIGHT);
      return;
    }

    setIsLoading(true);

    await fetchJsonp(
      `https://house.us10.list-manage.com/subscribe/post-json?u=dbf347e9af099367f9ef8e159&id=e7574a7b60&EMAIL=${emailValue}`,
      { jsonpCallback: 'c' }
    );

    await fetch(
      'https://86omjno35e.execute-api.us-east-1.amazonaws.com/dev/sendSlackNotification',
      {
        method: 'POST',
        body: JSON.stringify({
          channel: 'notifications-booked',
          icon_emoji: ':hand:',
          text: `Email: ${emailValue.substr(0, 2)}*****${emailValue.substr(
            emailValue.indexOf('@')
          )}
Language: ${languageCode}
Currency: ${currencyCode}
Pack: ${pack}
Price: ${currencySymbol}${price / 100}
                  `,
          username: 'Waitlist signup',
        }),
      }
    );

    setIsSubmitted(true);
  };

  const { emergencyNumber, samaritansNumber } =
    languageCode === 'en'
      ? {
          emergencyNumber: '999',
          samaritansNumber: '116 123',
        }
      : {
          emergencyNumber: '112',
          samaritansNumber: '717 003 717',
        };

  return (
    <div className={styles.container}>
      <div className={styles.content}>
        <h1 className={styles.logo}>Oliva</h1>
        {isSubmitted ? (
          <Fragment>
            <Paragraph>{strings.THANKS}</Paragraph>
            <Paragraph>
              {strings.WELL_BE_IN_TOUCH_WHEN_A_SPACE_OPENS_UP}
            </Paragraph>
            <Paragraph>
              {strings.IN_THE_MEANTIME_YOU_CAN}{' '}
              <a href="https://www.instagram.com/mind.chronicles/">
                {strings.READ_THERAPY_STORIES}
              </a>
              .
            </Paragraph>
          </Fragment>
        ) : (
          <Fragment>
            <Paragraph>{strings.WERE_CURRENTLY_NOT_TAKING}</Paragraph>
            <Paragraph>{strings.IF_YOUD_LIKE_US_TO_LET_YOU_KNOW}</Paragraph>
            <Paragraph>
              <i>
                {strings.OLIVA_IS_NOT_SUITABLE}{' '}
                <a
                  href={`tel:${emergencyNumber.replace(/\s/g, '')}`}
                  target="_blank"
                >
                  {emergencyNumber}
                </a>{' '}
                {strings.FOR_EMERGENCIES}{' '}
                <a href={`tel:${samaritansNumber.replace(/\s/g, '')}`}>
                  {samaritansNumber}
                </a>{' '}
                {strings.FOR_SAMARITANS}
              </i>
            </Paragraph>
            <div className={styles.error}>
              <Paragraph>{emailError}</Paragraph>
            </div>
            <input
              onChange={handleChange}
              placeholder="Email"
              type="text"
              value={emailValue}
            />
            <Button
              isDisabled={!!emailError}
              isLoading={isLoading}
              onClick={handleSubmit}
            >
              {strings.SIGN_UP}
            </Button>
          </Fragment>
        )}
      </div>
    </div>
  );
};

Component.displayName = 'CloseWindowWarner';
